@charset "UTF-8";
/** Media Queries **/
.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header-encabezado {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #64748B;
  width: 100%;
}
.dashboard__header-encabezado .dashboard__header-left {
  flex: 0 1 auto; /* Permite que ocupe solo el espacio necesario */
}
.dashboard__header-encabezado .dashboard__header-right {
  display: flex;
  align-items: center;
  justify-content: flex-end; /* Alinea los elementos a la derecha */
}
.dashboard__header-encabezado p {
  font-size: 1.8rem;
  margin: 0;
}
.dashboard__header-encabezado img, .dashboard__header-encabezado picture {
  height: 6rem;
  width: auto;
  margin-bottom: 1rem;
}
.dashboard .dashboard__settings {
  display: flex;
  align-items: center;
  margin-left: 15px;
  cursor: pointer;
}
.dashboard .fa-gear {
  font-size: 2rem;
  color: #64748B;
}
@media (min-width: 768px) {
  .dashboard__header-encabezado {
    flex-direction: row;
    align-items: center;
  }
  .dashboard__header-left, .dashboard__header-right {
    width: auto;
    justify-content: center;
  }
  .dashboard__header-right {
    margin-top: 1rem;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.6rem;
  display: block;
  text-align: center;
  cursor: pointer;
  margin-bottom: 2rem;
}
.dashboard__submit--logout:hover {
  color: #007df4;
}
@media (min-width: 768px) {
  .dashboard__submit--logout {
    margin-right: 2rem;
    font-size: 1.6rem;
  }
  .dashboard__submit--logout:last-of-type {
    margin-right: 0;
  }
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
  width: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard__enlace {
  color: #6f8ab6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #40587f;
  color: #FFFFFF;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
  text-align: center;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem 1rem 3rem 1rem;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
  }
}
.dashboard__boton {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  padding: 1rem 2.5rem;
  text-transform: uppercase;
  font-size: 1.4rem;
}
.dashboard__boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 2rem;
  overflow: scroll;
}
.dashboard__formulario {
  width: min(95%, 60rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .dashboard__contenedor-grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__progreso-contenedor {
  width: 100%;
  background-color: #b1c7dd;
  border-radius: 5px;
  overflow: hidden;
  height: 2rem;
}
.table__progreso {
  height: 100%;
  background-color: #007df4;
  border-radius: 5px;
  transition: width 0.5s ease;
}
.table__thead {
  color: #1E293B;
  border-bottom: 2px solid #64748B;
}
.table__th {
  padding: 1rem;
  text-align: center;
}
.table__tr {
  text-align: center;
}
.table__tr:nth-of-type(even) {
  background-color: #eaeef3;
}
.table__td, .table__td--acciones-th, .table__td--acciones-propiedades, .table__td--acciones {
  padding: 1.5rem 1rem;
  font-size: 1.2rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
.table__td--acciones-propiedades {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 1rem;
  gap: 2rem 0;
}
.table__td--acciones-th {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.table__td--acciones-th-div {
  display: flex;
  gap: 0 2rem;
}
.table__td-pregunta {
  text-align: left;
  padding: 1.5rem 1rem;
  font-size: 1.2rem;
}
.table__imagen {
  width: 15rem;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
  display: flex;
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion--privilegios {
  color: #323C4B;
}
.table__accion:hover {
  cursor: pointer;
}

/* Estilos base */
.resultados {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .resultados {
    flex-direction: row;
    justify-content: space-between;
  }
}

.boton_contenedor1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.resultados__genero, .resultados__filtros {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}
@media (min-width: 768px) {
  .resultados__genero, .resultados__filtros {
    flex-wrap: nowrap;
  }
}

.resultados__genero p {
  padding: 1rem;
  background-color: #007df4;
  border-radius: 2rem;
  color: #FFFFFF;
}
.resultados__genero span {
  font-weight: 700;
}

.resultados__filtros select {
  padding: 1rem;
  border-radius: 1rem;
  margin: 5px;
}
.resultados__filtros a {
  padding: 1rem;
  border-radius: 1rem;
  margin: 5px;
}

.calificacion-verde {
  color: #02db02; /* Un tono de verde */
  font-weight: 700;
}

.calificacion-amarillo {
  color: #F1A60E; /* Un tono de amarillo */
  font-weight: 700;
}

.calificacion-rojo {
  color: rgb(169, 0, 0); /* Un tono de rojo */
  font-weight: 700;
}

.inicio-admin h1 {
  margin-bottom: 1rem;
}
.inicio-admin h3 {
  font-weight: 400;
  font-size: 2rem;
  margin: 0 0 3rem 0;
}

.resumen__iconos {
  margin: 4rem 9rem 0 7rem;
}
@media (min-width: 480px) {
  .resumen__iconos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    margin: 2rem 0 0 10rem;
  }
}
@media (min-width: 768px) {
  .resumen__iconos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
    justify-content: space-around;
  }
}
.resumen__iconos-icono {
  display: flex;
  align-items: center;
  gap: 1rem; /* Espacio entre la imagen y el texto */
}
.resumen__iconos-icono img, .resumen__iconos-icono picture {
  width: 5rem;
}
.resumen__iconos-icono .labels {
  display: flex;
  flex-direction: column;
  text-align: left;
  font-size: 1.2rem;
}
.resumen__iconos-icono .labels p {
  color: #007df4;
  font-weight: 700;
  margin: 0.3rem 0;
}
.resumen__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  margin: 4rem 5rem 0 5rem;
}
@media (min-width: 768px) {
  .resumen__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.resumen__cuadro {
  text-align: center;
}
.resumen__bloque {
  text-align: center;
  padding: 1.5rem;
  border-radius: 1.5rem;
  background-color: #40587F;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.resumen__bloque img, .resumen__bloque picture {
  width: 12rem;
  height: 10rem;
}
.resumen__texto, .resumen__texto--periodo, .resumen__texto--Corregir, .resumen__texto--Mejorar, .resumen__texto--Capitalizar, .resumen__texto--Optimizar, .resumen__texto--numero {
  color: #FFFFFF;
  font-weight: 900;
  line-height: 1;
  margin: 1.5rem 0;
  font-size: 1.4rem;
  text-transform: uppercase;
}
.resumen__texto--numero {
  font-size: 1.4rem;
}
.resumen__texto--Capitalizar, .resumen__texto--Optimizar {
  font-size: 3.2rem;
  color: #02c202;
}
.resumen__texto--Mejorar {
  font-size: 3.2rem;
  color: #d9950d;
}
.resumen__texto--Corregir {
  font-size: 3.2rem;
  color: #900000;
}
.resumen__texto--periodo {
  color: #1a1b15;
  font-size: 1.5rem;
  font-weight: 700;
}
.resumen__texto span, .resumen__texto--numero span, .resumen__texto--Capitalizar span, .resumen__texto--Optimizar span, .resumen__texto--Mejorar span, .resumen__texto--Corregir span, .resumen__texto--periodo span {
  font-weight: 400;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4); /* Fondo semi-transparente */
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 50%; /* Ancho del modal */
  text-align: center; /* Alinear texto al centro */
}

.close {
  color: #000;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #d00;
  text-decoration: none;
  cursor: pointer;
}

.modal-content button {
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background-color: #007df4;
  color: white;
  display: block; /* Hace que los botones sean en bloque (uno debajo del otro) */
  width: 80%; /* Ancho de los botones */
  margin: 10px auto; /* Centra los botones y agrega margen vertical */
}

#confirmarEliminar {
  background-color: #007df4;
}

#cancelarEliminar {
  background-color: #dc3545;
}

button:hover {
  opacity: 0.8;
}

/* Estilos para pantallas de tablet en adelante */
@media (min-width: 768px) {
  .modal-content button {
    display: inline-block; /* Alinea los botones horizontalmente */
    width: auto; /* Ancho automático */
    margin-left: 20px; /* Margen solo a la izquierda */
    margin-right: 20px; /* Margen solo a la derecha */
  }
  .modal-content button:first-child {
    margin-right: 20px;
  }
  .modal-content button:last-child {
    margin-left: 20px;
  }
}
