html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

main {
  flex-grow: 1; /* Hace que el contenido principal ocupe todo el espacio disponible */
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: #1a1b15;
}

img,
picture {
  width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
