  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2.2rem;
  text-align: center;
}

h4 {
  font-size: 2rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 1.5rem;
  margin: 1rem 0 1rem 0;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenedor {
