}
.admin thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.admin thead tr {
  text-align: center;
}
.admin .imagen-tabla {
  width: 10rem;
}

.bold {
  font-weight: bold;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
