  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  width: auto;
  align-self: flex-start;
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}
.formulario__checkboxes {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  font-size: 1.4rem;
}
.formulario__checkbox {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #1E293B;
}
.formulario__checkbox input[type=checkbox] {
  width: 20px;
  height: 20px;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  padding: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: 1px solid #b1c7dd;
}
.formulario__checkbox input[type=checkbox]:checked {
  background-color: #02db02;
  border-color: #02db02;
}
.formulario__checkbox label {
  font-size: 1.4rem;
  color: #1E293B;
  cursor: pointer;
  padding-left: 1rem;
}
.formulario__checkbox label:hover {
  color: #001F5B;
}
.formulario__checkbox input[type=checkbox]:focus {
  outline: none;
  box-shadow: 0 0 5px #001F5B;
}
.formulario__checkbox input[type=checkbox]:checked + label {
  font-weight: normal;
}

