/* Estilos Generales */
.grid-contenedor {
  margin-top: 3rem;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
.grid-contenedor img, .grid-contenedor picture {
  width: 90%;
  max-width: 600px; /* Ancho como porcentaje del contenedor */
  height: auto; /* La altura se ajusta manteniendo la proporción de la imagen */
  margin: 3rem auto;
}
@media (min-width: 768px) {
  .grid-contenedor {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .grid-contenedor img, .grid-contenedor picture {
    width: 95%;
    max-width: 600px;
    height: auto; /* La altura se ajusta manteniendo la proporción de la imagen */
    margin: 3rem auto;
  }
}
@media (min-width: 1024px) {
  .grid-contenedor img, .grid-contenedor picture {
    width: 80%;
    max-width: 600px;
    height: auto; /* La altura se ajusta manteniendo la proporción de la imagen */
    margin: 3rem auto;
  }
}

.contenedor-encuesta {
  display: flex;
  flex-wrap: wrap;
}
.contenedor-encuesta p {
  text-align: left;
}

.encuesta__formulario {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
